* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1448px;
  width: 100%;
  margin: 0 auto;
  padding: 0 158px;

  @media (max-width: $tablet-breakpoint) {
    padding: 0 50px;
  }

  @media (max-width: $mobile-breakpoint) {
    padding: 0 20px;
  }
}